@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:700);
/* @import url('https://fonts.googleapis.com/css2?family=font-effect-fire-animation'); */
/*@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed'); */

:root {
  /* --primary-color: rgb(75, 0, 130); */
  /* --primary-color: #4285f4; */
  --primary-color: #6E07F3;
  --secondary-color: #25d4ac;
  --third-color: rgb(33, 35, 37);
  --card-back-text: rgba(255, 255, 255, 0.904);
  --anchor-color: #25d4ac;
}

* {
  font-family: roboto, arial, helvetica, sans-serif;
}


.portfolio {
  background-color: rgb(33, 35, 37);
  background-color: var(--third-color);
  /* background-color: #6E07F3; */
  /* background-image: url(../images/Pngtree.jpg); */
  /* background-image: url(../images/home-page.jpg); */
}

.container {
  margin-top: 3rem;

}

main {
  min-height: calc(100vh - 270px);
}

.about, .work-title, .contact-title, #resume{
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: #6E07F3;
  color: var(--primary-color);
}

/* About */

.about {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px); 
  /* background-color: rgba(255, 255, 255, 0.7); */
  background-color: #6E07F3;
  background-color: var(--primary-color);
  color: rgba(255, 255, 255, 0.904);
  color: var(--card-back-text);

  outline-style:groove;
  outline-color: #6E07F3;
  outline-color: var(--primary-color);
}

#pfp {
  width: 250px;
  border-radius: 100%;
  padding-bottom: 2rem;
}

p {
  font-size: 1.3rem;
}

/* NAVBAR */

#nav {
  height: 75px;
/**/  background-color: #6E07F3;  background-color: var(--primary-color); /**/
}

.justify-content-center {
    justify-content: center!important;
}

.nav {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
  }

.nav-item li {
  color: rgb(33, 35, 37);
  color: var(--third-color);
}

.nav-item span {
  color: rgb(33, 35, 37);
  color: var(--third-color);
  font-size: 1.5rem;
  padding-top: 2rem;
}

.nav-item span:hover {
  color: #25d4ac;
  color: var(--anchor-color);
  cursor: pointer;
}

.nav-link {
    display: block;
    padding: 1rem 1rem;
}

.nav-link span{
  color: #25d4ac;
  color: var(--secondary-color);

}

.navActive span{
  color: rgba(255, 255, 255, 0.904);
  color: var(--card-back-text);
  text-decoration: none;
}

/* PROJECTS */

.project-items {
  width: 23.438rem;
  height: 12.5rem;
  border: 2px solid black;
  margin: 2rem;
  padding: 0 !important;
  position: relative;
  transition: -webkit-transform .15s;
  transition: transform .15s;
  transition: transform .15s, -webkit-transform .15s;
}

.project-items:hover {
  -webkit-transform: scale(1.13);
          transform: scale(1.13);
}

.projects {
  padding-top: 2rem;
}

.project-items img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /*additions when hover is removed*/
  -webkit-filter: blur(3px) brightness(40%);
          filter: blur(3px) brightness(40%);
}

.image-text {
  position:absolute;
  top: 8px;
  left: 16px;
  visibility: hidden;
  opacity: 0;
  transition: opacity .4s, visibility .6s;
  color: #25d4ac;
  color: var(--secondary-color);
}

.image-text h4 {
  font-weight: bolder;
}

.image-text p {
  /*color: var(--card-back-text);*/
  color: rgba(255, 255, 255, 0.904);
  color: var(--card-back-text);
  font-size: 1rem;
}

.image-text a {
  text-decoration: none;
  color: #25d4ac;
  color: var(--secondary-color);
  padding-right: 1rem;
}

.image-text a:hover {
  color: rgba(255, 255, 255, 0.904);
  color: var(--card-back-text);
}
/* REMOVED ( .project-items:hover ) */
.project-items .image-text {
  visibility: visible;
  opacity: 2;
}
/*
.project-items:hover img{
  filter: blur(3px) brightness(50%);
} */

/* Contact */

#contact-form {
  padding-left: 10%;
  padding-right: 10%;
}

#contact-form .btn{
  color: #25d4ac;
  color: var(--secondary-color);
  background-color: #6E07F3;
  background-color: var(--primary-color);
}

/* RESUME*/

.container #resume {
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

#resume .btn{
  background-color: #6E07F3;
  background-color: var(--primary-color);
  transition: .3s;
  margin-bottom: 30px;
}

#resume a {
  text-decoration: none;
  color: #25d4ac;
  color: var(--secondary-color);
}

#resume .btn:hover {
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
  transition: .3s;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  70% {
    -webkit-transform: scale(.9);
            transform: scale(.9);
  }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  70% {
    -webkit-transform: scale(.9);
            transform: scale(.9);
  }
    100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


.skills-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: row;
  grid-column-gap: 7rem;
  -webkit-column-gap: 7rem;
          column-gap: 7rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.skills-wrapper ul {
  padding: 0;
}

.skills-wrapper li {
  list-style-type: none;
  font-size: 1.3rem;
}

/* FOOTER */

footer h3 {
  font-weight: bold;
  font-size: 1.8rem;
 
}

footer {
  height: 100px;
  border-top: 2px solid #6E07F3;
  border-top: 2px solid var(--primary-color);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
}

footer p {
  margin-top: 1rem;
}

.socials {
/**/  color: #6E07F3;  color: var(--primary-color); /**/
  width: 100%;
  text-align: center;
  margin-top: auto;
}

footer i {
